import React from 'react';
import { default as MuiBadge, BadgeProps } from '@mui/material/Badge';

const Badge = React.forwardRef<HTMLElement, BadgeProps>( ( props, ref ) => {
	return (
		<MuiBadge
			{ ...props }
			ref={ ref as React.MutableRefObject<null> }
		/>
	);
} ) as React.FC<BadgeProps>;

export default Badge;
